import StaticConfigModel from "../model/StaticConfig/StaticConfigModel";
import JishiModel from "../model/jishi/JishiModel";
import MainModel from "../model/main/MainModel";



export default class ModelManager {
    /**主界面数据类 */
    public mainModel: MainModel;
    public jishiModel:JishiModel;
    public configModel: StaticConfigModel;

    constructor() {
        let s = this;
        s.register();
    }
    private register(): void {
        let s = this;
        s.mainModel = new MainModel();
        s.jishiModel = new JishiModel();
        s.configModel = new StaticConfigModel();


    }
    public init(){
        this.mainModel.init();
    }
}